import React from 'react';

interface Option {
  value: string;
  name: string;
}

interface Props extends React.SelectHTMLAttributes<HTMLSelectElement> {
  label: string;
  value: string;
  onChange: (event: React.ChangeEvent) => void;
  options?: Option[];
}

const SelectBox = ({ label, value, onChange, options, ...props }: Props) => {
  const id = Math.floor(Date.now() * Math.random() * Math.pow(10, 3)).toString(16);
  return (
    <div className={`inputbox select`}>
      <label className="inputbox-label" htmlFor={id}>{label}</label>
      <select className="inputbox-select" id={id} value={value} onChange={onChange} {...props}>
        {options?.map(({ value, name }) => (
          <option key={value} value={value}>
            {name}
          </option>
        ))}
      </select>
    </div>
  );
};

export default SelectBox;
