import dayjs from 'dayjs';
import React, { useEffect, useRef, useState } from 'react';
import useFabricCanvas from '../../hooks/useFabricCanvas';
import { saveToFile } from '../../utils/file-transfer';
import CanvasBG from './CanvasBG';
import InputBox from './InputBox';
import closeIcon from '../../assets/svg/close.svg';

const width = 630;
const height = 300;

interface Props {
  imageURL: string;
  close: () => void;
}

const SaveModal = ({ imageURL, close }: Props) => {
  const [loaded, setLoaded] = useState<Boolean>(false);
  const [isCropperAdded, setIsCropperAdded] = useState<Boolean>(false);

  const defaultFilename = 'stibee_divider_' + dayjs().format('YYYYMMDD');
  const [filename, setFilename] = useState(defaultFilename);

  const [cropping, setCropping] = useState<{
    top?: number;
    height?: number;
  }>({});

  const ref = useRef<HTMLCanvasElement>();

  const { canvas, init, addImage, addCropper, selectObject, getImage } = useFabricCanvas(
    width,
    height
  );

  useEffect(() => {
    const canvasElement = ref.current;
    if (!canvasElement || canvas) return;

    init(canvasElement);
  }, [canvas, init]);

  useEffect(() => {
    if (canvas) {
      setLoaded(true);
    }
  }, [canvas]);

  useEffect(() => {
    if (!loaded) return;

    addImage(
      imageURL,
      { x: width / 2, y: height / 2 },
      { hoverCursor: 'default', selectable: false, objectCaching: false },
      (img) => {
        img.sendToBack();
      }
    );

    if (isCropperAdded) return;

    const cropper = addCropper();

    cropper.on('scaled', () => {
      const { top, height } = cropper.getBoundingRect();
      setCropping({ top, height });
    });

    cropper.on('deselected', () => {
      selectObject(cropper);
    });

    setIsCropperAdded(true);
  }, [addCropper, addImage, imageURL, isCropperAdded, loaded, selectObject]);

  return (
    <>
      <div className="modal-background" onClick={() => close()}></div>
      <dialog className="modal" open>
        <button className="close" onClick={() => close()}>
          <img className="icon" src={closeIcon} alt="닫기" />
        </button>

        <header className="modal-header">
          <h1 className="title">만든 구분선을 이미지로 저장하세요</h1>
        </header>

        <section className="modal-section">
          <div className="header">
            <span className="title">높이 조절</span>
          </div>

          <p className="desc">조절선을 움직여 빈 영역을 자를 수 있습니다.</p>
          <div className="container">
            <CanvasBG width={width} height={height} />
            <canvas
              className="canvas"
              ref={ref as React.MutableRefObject<HTMLCanvasElement>}
            ></canvas>
          </div>
        </section>

        <footer className="modal-footer">
          <InputBox
            label="파일 이름"
            value={filename}
            placeholder={defaultFilename}
            onChange={(event: React.ChangeEvent) => {
              const value = (event.target as HTMLInputElement).value.trim();
              setFilename(value);
            }}
          />

          <button
            className="save"
            onClick={() => {
              getImage(imageURL, (image) => {
                const data = image.toDataURL({ top: cropping.top, height: cropping.height });
                saveToFile(data, filename);
              });
            }}
          >
            저장하기
          </button>
        </footer>
      </dialog>
    </>
  );
};

export default SaveModal;
