import { Circle } from 'fabric/fabric-impl';
import React, { useState } from 'react';
import { matchColorFormat, toTwoDigitsHex } from '../../utils/color';
import ColorInputBox from './ColorInputBox';
import InputBox from './InputBox';
import InputContainer from './InputContainer';

interface Props {
  item: Circle;
  renderAll: () => void;
}

const CircleControl = ({ item, renderAll }: Props) => {
  const { canvas } = item;
  const [radius, setRadius] = useState(item.radius);
  const [fillColor, setFillColor] = useState(item.fill as string);
  const [fillOpacity, setFillOpacity] = useState(100);
  const [startAngle, setStartAngle] = useState(360 - item.startAngle!);

  return (
    <>
      <InputBox
        label="반지름"
        className="controller-input"
        name="radius"
        type="range"
        value={radius}
        min={1}
        max={canvas!.height! / 2}
        onChange={(event: React.ChangeEvent) => {
          const value = Number((event.target as HTMLInputElement).value);
          item.set('radius', value);
          setRadius(value);
          renderAll();
        }}
      />
      <InputBox
        label="중심각"
        className="controller-input"
        name="startAngle"
        type="range"
        value={startAngle}
        min={0}
        max={360}
        onChange={(event: React.ChangeEvent) => {
          const value = Number((event.target as HTMLInputElement).value);
          item.set('startAngle', ((360 - value) * Math.PI) / 180);
          setStartAngle(value);
          renderAll();
        }}
      />
      <InputContainer
        Super={
          <ColorInputBox
            label="채우기 색상"
            name="fillColor"
            value={fillColor}
            onChange={(event: React.ChangeEvent) => {
              const value = (event.target as HTMLInputElement).value.trim();
              setFillColor(value);

              const colorCode = matchColorFormat(value);

              if (colorCode) {
                const opacityHex = toTwoDigitsHex(fillOpacity);
                const colorCodeWithOpacity = colorCode + opacityHex;

                item.set('fill', colorCodeWithOpacity);
              } else if (!value) {
                item.set('fill', undefined);
              }
              renderAll();
            }}
          />
        }
        condition={Boolean(fillColor)}
        Sub={
          <>
            <InputBox
              label="채우기 투명도"
              className="control-panel-input"
              name="opacity"
              type="range"
              value={fillOpacity}
              min={0}
              max={100}
              onChange={(event: React.ChangeEvent) => {
                const value = Number((event.target as HTMLInputElement).value);
                setFillOpacity(value);

                const hex = toTwoDigitsHex(value);
                const colorCode = matchColorFormat(fillColor);

                if (colorCode) {
                  const colorCodeWithOpacity = colorCode + hex;
                  item.set('fill', colorCodeWithOpacity);
                  renderAll();
                }
              }}
            />
          </>
        }
      />
    </>
  );
};

export default CircleControl;
