import React from 'react';

interface Option {
  content: string | React.ReactNode;
  onClick: (event: React.MouseEvent) => void;
}

interface Props {
  label: string;
  options?: Option[];
}

const SelectButtons = ({ label, options }: Props) => {
  return (
    <div className={`inputbox`}>
      <span className="inputbox-label">{label}</span>
      <div className="inputbox-buttons">
        {options?.map(({ content, onClick }, i) => (
          <button className="inputbox-buttons-button" key={i} onClick={onClick}>
            {content}
          </button>
        ))}
      </div>
    </div>
  );
};

export default SelectButtons;
