import React, { useEffect, useRef, useState } from 'react';
import useFabricCanvas from '../../hooks/useFabricCanvas';
import CanvasBG from './CanvasBG';
import ControlPanel from './ControlPanel';
import DownloadBtn from './DownloadBtn';
import ToolSelector from './ToolBox';
import SaveModal from './SaveModal';

const width = 630;
const height = 300;

const DividerGenerator = () => {
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [loaded, setLoaded] = useState(false);
  const canvasRef = useRef<HTMLCanvasElement>();
  const {
    tool,
    currentSelected,
    canvas,
    init,
    observer,
    drawItem,
    changeTool,
    changeCurrentSelected,
    removeObserver,
    renderAll,
    removeSelected,
    duplicateSelected,
    toDataURL,
  } = useFabricCanvas(width, height);

  // fabric 초기화
  useEffect(() => {
    const canvasElement = canvasRef.current;
    if (!canvasElement || canvas) return;

    init(canvasElement);
  }, [canvas, init]);

  useEffect(() => {
    if (canvas) {
      setLoaded(true);
    }
  }, [canvas]);

  useEffect(() => {
    if (loaded) {
      observer('mouse:down', (options) => {
        drawItem(options.pointer);
        changeTool('select');
      });

      observer('object:added', (options) => {
        const added = options.target!;
        changeTool('select');

        added.on('selected', ({ target }) => {
          changeCurrentSelected(target);
        });

        added.on('deselected', () => {
          changeCurrentSelected(undefined);
        });
      });

      return () => {
        // 이벤트 리스너를 매 렌더링 때마다 제거하고 새로 추가 해주어야 함
        removeObserver('mouse:down');
        removeObserver('object:added');
      };
    }
  }, [changeCurrentSelected, changeTool, drawItem, loaded, observer, removeObserver]);

  return (
    <>
      <section className="generator">
        <div className="toolbox">
          <ToolSelector current={tool} changeTool={changeTool} />
        </div>
        <div className="container">
          <CanvasBG width={width} height={height} />
          <canvas
            className="canvas"
            ref={canvasRef as React.MutableRefObject<HTMLCanvasElement>}
          >
            <p className="alt">
              지원되지 않는 브라우저 입니다.
              <br />
              최신 버전의 브라우저를 사용하세요.
              <br />
              (Chrome 브라우저 사용을 권장합니다.)
            </p>
          </canvas>
        </div>
        <ControlPanel
          canvas={canvas}
          currentSelected={currentSelected}
          renderAll={renderAll}
          removeSelected={removeSelected}
          duplicateSelected={duplicateSelected}
        />
        <DownloadBtn onClick={() => setIsModalOpen(true)} />
      </section>
      {isModalOpen && <SaveModal imageURL={toDataURL()} close={() => setIsModalOpen(false)} />}
    </>
  );
};

export default DividerGenerator;
