import React from 'react';
import { Tool } from '../../hooks/useFabricCanvas';
import circle from '../../assets/svg/circle.svg';
import rect from '../../assets/svg/rect.svg';
import select from '../../assets/svg/select.svg';
import curved from '../../assets/svg/curved.svg';
import text from '../../assets/svg/text.svg';
import image from '../../assets/svg/image.svg';

interface Props {
  current: Tool;
  changeTool: (tool: Tool) => void;
}

const tools = [
  {
    type: 'select',
    name: '선택 도구',
    icon: select,
  },
  {
    type: 'line',
    name: '선 도구',
    icon: curved,
  },
  {
    type: 'circle',
    name: '원형 도구',
    icon: circle,
  },
  {
    type: 'rect',
    name: '사각형 도구',
    icon: rect,
  },
  {
    type: 'text',
    name: '텍스트 도구',
    icon: text,
  },
  {
    type: 'image',
    name: '이미지 도구',
    icon: image,
  },
];

const ToolBox = ({ current, changeTool }: Props) => {
  return (
    <div className="tools">
      {tools.map(({ type, name, icon }) => (
        <button
          key={name}
          className={current === type ? 'tool selected' : 'tool'}
          onClick={() => {
            changeTool(type as Tool);
          }}
        >
          <img className="icon" src={icon} alt={name} />
        </button>
      ))}
    </div>
  );
};

export default ToolBox;
