import { Object } from 'fabric/fabric-impl';
import React, { useState } from 'react';
import ColorInputBox from './ColorInputBox';
import InputBox from './InputBox';
import SelectBox from './SelectBox';
import SelectButtons from './SelectButtons';
import align_left from '../../assets/svg/align-left.svg';
import align_center from '../../assets/svg/align-center.svg';
import align_right from '../../assets/svg/align-right.svg';
import InputContainer from './InputContainer';
import { matchColorFormat, toTwoDigitsHex } from '../../utils/color';

interface Props {
  item: Object;
  renderAll: () => void;
}

type StrokeType = 'line' | 'dashed-1' | 'dashed-2';

const strokeTypes = [
  {
    value: 'line',
    name: '실선',
  },
  {
    value: 'dashed-1',
    name: '점선',
  },
  {
    value: 'dashed-2',
    name: '긴 점선',
  },
];

function convertStrokeDashArray(value: StrokeType) {
  switch (value) {
    case 'line':
      return undefined;
    case 'dashed-1':
      return [3, 5];
    case 'dashed-2':
      return [10, 10];
    default:
      return undefined;
  }
}

const BaseControl = ({ item, renderAll }: Props) => {
  const initialStrokeColor = matchColorFormat(item.stroke as string) || '';

  const [opacity, setOpacity] = useState(item.opacity ? item.opacity * 100 : 1);
  const [skewX, setSkewX] = useState(item.skewX || 0);
  const [skewY, setSkewY] = useState(item.skewY || 0);
  const [strokeWidth, setStrokeWidth] = useState(item.strokeWidth);
  const [strokeType, setStrokeType] = useState(strokeTypes[0].value);
  const [strokeColor, setStrokeColor] = useState(initialStrokeColor);

  const alignments = [
    {
      content: <img className="icon" src={align_left} alt="왼쪽 정렬" />,
      onClick: () => {
        const { height } = item.canvas!;
        const itemWidth = item.getBoundingRect().width;
        item.set({ left: 0 + itemWidth / 2, top: height! / 2 });
        renderAll();
      },
    },
    {
      content: <img className="icon" src={align_center} alt="가운데 정렬" />,
      onClick: () => {
        const { width, height } = item.canvas!;
        item.set({ left: width! / 2, top: height! / 2 });
        renderAll();
      },
    },
    {
      content: <img className="icon" src={align_right} alt="오른쪽 정렬" />,
      onClick: () => {
        const { width, height } = item.canvas!;
        const itemWidth = item.getBoundingRect().width;
        item.set({ left: width! - itemWidth / 2, top: height! / 2 });
        renderAll();
      },
    },
  ];

  return (
    <>
      <SelectButtons label="정렬" options={alignments} />
      <InputBox
        label="투명도"
        className="control-panel-input"
        name="opacity"
        type="range"
        value={opacity}
        min={0}
        max={100}
        onChange={(event: React.ChangeEvent) => {
          const value = Number((event.target as HTMLInputElement).value);
          item.set('opacity', value / 100);
          setOpacity(value);
          renderAll();
        }}
      />
      <InputBox
        label="기울임(가로)"
        className="control-panel-input"
        name="skewX"
        type="range"
        value={skewX}
        min={-50}
        max={50}
        onChange={(event: React.ChangeEvent) => {
          const value = Number((event.target as HTMLInputElement).value);
          item.set('skewX', value);
          setSkewX(value);
          renderAll();
        }}
      />
      <InputBox
        label="기울임(세로)"
        className="control-panel-input"
        name="skewY"
        type="range"
        value={skewY}
        min={-50}
        max={50}
        onChange={(event: React.ChangeEvent) => {
          const value = Number((event.target as HTMLInputElement).value);
          item.set('skewY', value);
          setSkewY(value);
          renderAll();
        }}
      />
      <InputContainer
        Super={
          <ColorInputBox
            label="선 색상"
            name="fillColor"
            value={strokeColor}
            onChange={(event: React.ChangeEvent) => {
              const value = (event.target as HTMLInputElement).value.trim();
              setStrokeColor(value);

              const colorCode = matchColorFormat(value);

              if (colorCode) {
                const opacityHex = toTwoDigitsHex(opacity);
                const colorCodeWithOpacity = colorCode + opacityHex;

                item.set('stroke', colorCodeWithOpacity);
              } else if (!value) {
                item.set('stroke', 'transparent');
              }
              renderAll();
            }}
          />
        }
        condition={Boolean(strokeColor)}
        Sub={
          <>
            <SelectBox
              label="선 종류"
              name="strokeDashArray"
              options={strokeTypes}
              value={strokeType}
              onChange={(event: React.ChangeEvent) => {
                const value = (event.target as HTMLSelectElement).value;
                const strokeDashArray = convertStrokeDashArray(value as StrokeType);
                item.set('strokeDashArray', strokeDashArray);
                setStrokeType(value);
                renderAll();
              }}
            />
            <InputBox
              label="선 두께"
              className="control-panel-input"
              name="strokeWidth"
              type="range"
              value={strokeWidth}
              min={0}
              max={10}
              step={0.1}
              valueStep={0.1}
              onChange={(event: React.ChangeEvent) => {
                const value = Number((event.target as HTMLInputElement).value);
                item.set('strokeWidth', value);
                setStrokeWidth(value);
                renderAll();
              }}
            />
          </>
        }
      />
    </>
  );
};

export default BaseControl;
