import React from 'react';

interface Props {
  Super: React.ReactNode;
  Sub: React.ReactNode;
  condition?: boolean;
}

const InputContainer = ({ Super, Sub, condition = true }: Props) => (
  <div className="input-container">
    {Super}
    {condition && <div className="input-container-sub">{Sub}</div>}
  </div>
);

export default InputContainer;
