import React, { useRef } from 'react';

interface Props extends React.InputHTMLAttributes<HTMLInputElement> {
  label?: string;
  className?: string;
  name?: string;
  value?: any;
  onChange?: (event: React.ChangeEvent) => void;
}

const ColorInputBox = ({ label, className, name, value, onChange, ...props }: Props) => {
  const id = Math.floor(Date.now() * Math.random() * Math.pow(10, 3)).toString(16);
  const ref = useRef<HTMLInputElement>();
  const colorValue = value && value.length > 6 ? value.slice(0, 7) : value;
  return (
    <div className="inputbox">
      <label className="inputbox-label" htmlFor={id}>
        {label}
      </label>
      <div className="inputbox-color">
        <input
          id={id}
          className="inputbox-color-text"
          type="text"
          value={value}
          placeholder="색상 없음"
          onChange={onChange}
        />
        <div className="inputbox-color-wrapper" style={{ background: value }}>
          <input
            ref={ref as React.MutableRefObject<HTMLInputElement>}
            className="inputbox-color-input"
            name={name}
            type="color"
            value={colorValue}
            onChange={onChange}
            {...props}
          />
        </div>
      </div>
    </div>
  );
};

export default ColorInputBox;
