import { fabric } from 'fabric';

const Cropper = fabric.util.createClass(fabric.Rect, {
  initialize: function (width: number, height: number) {
    this.callSuper('initialize', {
      originX: 'center',
      originY: 'center',
      left: width / 2,
      top: height / 2,
      width,
      height,
      fill: undefined,
      perPixelTargetFind: true,
      excludeFromExport: true,
      objectCaching: false,
      hasBorders: false,
      lockMovementX: true,
      lockMovementY: true,
    });

    const { mt: middleTopControl, mb: middleBottomControl } = fabric.Object.prototype.controls;

    const controlOptions = {
      x: 0,
      offsetX: 0,
      offsetY: -5,
      sizeX: 630,
      sizeY: 20,
      cursorStyle: 'pointer',
    };

    const cropTopControl = new fabric.Control({
      y: -0.5,
      actionHandler: middleTopControl.actionHandler,
      cursorStyleHandler: middleTopControl.cursorStyleHandler,
      render: function (
        ctx: CanvasRenderingContext2D,
        left: number,
        top: number,
        styleOverride: any,
        fabricObject: fabric.Rect
      ) {
        const width = fabricObject.width!;
        const offsetY = Math.abs(this.offsetY!);
        const lineWidth = 5;

        ctx.fillStyle = '#bcbcbc80';
        ctx.fillRect(0, 0, width, top + offsetY + lineWidth);

        ctx.strokeStyle = '#ff6464';
        ctx.lineWidth = lineWidth;
        ctx.beginPath();
        ctx.moveTo(0, top + offsetY + lineWidth);
        ctx.lineTo(width, top + offsetY + lineWidth);
        ctx.stroke();
      },
      ...controlOptions,
    });

    const cropBottomControl = new fabric.Control({
      y: 0.5,
      actionHandler: middleBottomControl.actionHandler,
      cursorStyleHandler: middleBottomControl.cursorStyleHandler,
      render: function (
        ctx: CanvasRenderingContext2D,
        left: number,
        top: number,
        styleOverride: any,
        fabricObject: fabric.Rect
      ) {
        const { canvas } = fabricObject;
        const width = fabricObject.width!;
        const offsetY = Math.abs(this.offsetY!);
        const lineWidth = 5;

        ctx.fillStyle = '#bcbcbc80';
        ctx.fillRect(0, top + offsetY - lineWidth, width!, canvas!.height! - top);

        ctx.strokeStyle = '#ff6464';
        ctx.lineWidth = lineWidth;
        ctx.beginPath();
        ctx.moveTo(0, top + offsetY - lineWidth);
        ctx.lineTo(width, top + offsetY - lineWidth);
        ctx.stroke();
      },
      ...controlOptions,
    });

    this.controls = { mt: cropTopControl, mb: cropBottomControl };
  },
  _render: function (ctx: CanvasRenderingContext2D) {
    this.callSuper('_render', ctx);
  },
});

export default Cropper;
