import { Rect } from 'fabric/fabric-impl';
import React, { useState } from 'react';
import { matchColorFormat, toTwoDigitsHex } from '../../utils/color';
import ColorInputBox from './ColorInputBox';
import InputBox from './InputBox';
import InputContainer from './InputContainer';

interface Props {
  item: Rect;
  renderAll: () => void;
}

const RectController = ({ item, renderAll }: Props) => {
  const [fillColor, setFillColor] = useState(item.fill as string);
  const [fillOpacity, setFillOpacity] = useState(100);

  return (
    <>
      <InputContainer
        Super={
          <ColorInputBox
            label="채우기 색상"
            name="fillColor"
            value={fillColor}
            onChange={(event: React.ChangeEvent) => {
              const value = (event.target as HTMLInputElement).value.trim();
              setFillColor(value);

              const colorCode = matchColorFormat(value);

              if (colorCode) {
                const opacityHex = toTwoDigitsHex(fillOpacity);
                const colorCodeWithOpacity = colorCode + opacityHex;

                item.set('fill', colorCodeWithOpacity);
              } else if (!value) {
                item.set('fill', undefined);
              }
              renderAll();
            }}
          />
        }
        condition={Boolean(fillColor)}
        Sub={
          <>
            <InputBox
              label="채우기 투명도"
              className="control-panel-input"
              name="opacity"
              type="range"
              value={fillOpacity}
              min={0}
              max={100}
              onChange={(event: React.ChangeEvent) => {
                const value = Number((event.target as HTMLInputElement).value);
                setFillOpacity(value);

                const hex = toTwoDigitsHex(value);
                const colorCode = matchColorFormat(fillColor);

                if (colorCode) {
                  const colorCodeWithOpacity = colorCode + hex;
                  item.set('fill', colorCodeWithOpacity);
                  renderAll();
                }
              }}
            />
          </>
        }
      />
    </>
  );
};

export default RectController;
