import React, { useState } from 'react';
import { Canvas } from 'fabric/fabric-impl';
import ColorInputBox from './ColorInputBox';
import InputBox from './InputBox';
import InputContainer from './InputContainer';
import { matchColorFormat, toTwoDigitsHex } from '../../utils/color';

interface Props {
  canvas?: Canvas;
  renderAll: () => void;
}

const GlobalControl = ({ canvas, renderAll }: Props) => {
  const initialFill = canvas?.fill ? String(canvas?.fill) : '';
  const hex = initialFill.length === 9 ? initialFill.slice(-2) : 'ff';
  const initialOpacity = Math.floor((parseInt(hex, 16) / 255) * 100);

  const [fillColor, setFillColor] = useState(initialFill);
  const [opacity, setOpacity] = useState(initialOpacity);

  return (
    <>
      <InputContainer
        Super={
          <ColorInputBox
            label="배경 색상"
            name="fillColor"
            value={fillColor}
            onChange={(event: React.ChangeEvent) => {
              if (!canvas) return;
              const value = (event.target as HTMLInputElement).value.trim();
              setFillColor(value);

              const colorCode = matchColorFormat(value);

              if (colorCode) {
                const opacityHex = toTwoDigitsHex(opacity);
                const colorCodeWithOpacity = colorCode + opacityHex;

                canvas.backgroundColor = colorCodeWithOpacity;
              } else if (!value) {
                canvas.backgroundColor = undefined;
              }
              renderAll();
            }}
          />
        }
        condition={Boolean(fillColor)}
        Sub={
          <InputBox
            label="배경 투명도"
            className="control-panel-input"
            name="opacity"
            type="range"
            value={opacity}
            min={0}
            max={100}
            onChange={(event: React.ChangeEvent) => {
              if (!canvas) return;
              const value = Number((event.target as HTMLInputElement).value);
              setOpacity(value);

              const hex = toTwoDigitsHex(value);
              const colorCode = matchColorFormat(fillColor);

              if (colorCode) {
                const colorCodeWithOpacity = colorCode + hex;
                canvas!.backgroundColor = colorCodeWithOpacity;
                renderAll();
              }
            }}
          />
        }
      />
    </>
  );
};

export default GlobalControl;
